.comments{
   display: flex;
   flex-direction: column;
   border: 1px solid $greyColor;
   padding: $freeSpace;
   margin-bottom: $freeSpace;
   background-color: $whiteColor;
   border-radius: $borderRadius;

   h3{
       margin-top: 0;
   }
   form.comment{
       display: none;
       flex-direction: column;
       max-width: 500px;
       textarea{
           margin-bottom: $freeSpace*0.8;
       }
       .buttons{
           align-self: flex-end;
           display: flex;
           button{
               margin-left: $freeSpace;
               height: 24px;
               font-size:0.9em;
           }
       }
   }
   .comment-list{
       ul{
           list-style: none;
           border-left: 1px solid $greyColor;
           padding-left: $freeSpace*1.2;
           &:first-child{
            padding-left: 0;
            border-left: 0;
            }
        }
       li{
           display: flex;
           flex-direction: column;
           margin-top: $freeSpace;
           .head{
               display:flex;
               position: relative;
               background-color: rgba(232, 232, 232,0.4);
              
            .date{
                margin-left: 7px;
                font-style: italic;
                color: lighten($blackColor,45);
            }
            .head-delete{
                position: absolute;
                display: none;
                right: 0;
                border: 0;
                font-size: 20px;
                color: $primaryColor;
                background-color: transparent;
            }
          
           }
           p.deleted{
               padding: 10px 0px 10px 0px;
            font-style: italic;
            font-size: 12px;
            color:$greyColor;
        }
       }
       p{
           padding: 10px 0px 10px 0px;
       }
       .head:hover .head-delete{
        display:block;
       }
    }
   
}