    padding-right: 15px;
    display: flex;
    align-items: center;
    border-radius: 2px; }
    form button:active,
    form .button:active {
      box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.2); }
    form button:hover,
    form .button:hover {
      text-decoration: none; }
    form button.secondary,
    form .button.secondary {
      background-color: #f8ba3d;
      border: 1px solid #f8ba3d; }
    form button.secondary-inverse,
    form .button.secondary-inverse {
