      border-color: #a3c8d6;
      background-color: #fff; }
  form input,
  form textarea {
    background-color: #fff;
    border: 1px solid #e8e8e8;
    display: table;
    border-collapse: separate;
    overflow: hidden;
    position: relative;
    width: 100%;
    font-size: 14px;
    border-radius: 2px; }
    form input:disabled,
    form textarea:disabled {
